$base-color: #55298A;
$help-color: #e2dcec;
$accent-color: #ffa800;
$light-grey: #eee;
$violet-grey: #3b3444;
$white: #fff;
$black: #000;

@mixin lato {
	font-family: 'Lato', sans-serif;
	font-weight: normal; }

@mixin slab {
	font-family: 'Roboto Slab', serif;
	font-weight: normal; }

body {
	min-width: 320px;
	@include lato;
	background: $white;
	line-height: 1.4;
	&.is-overflow {
		overflow: hidden; } }

a {
	&:hover {
		text-decoration: none; } }

.container {
	&.container--center {
		max-width: 1300px; } }

.overlay {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: rgba($black, 0.4);
	opacity: 0;
	visibility: hidden;
	&.is-active {
		opacity: 1;
		visibility: visible; } }

/* titles */
.title-h1,
.title-h2,
.title-h3,
.title-h4,
.title-h5,
.title-h6 {
	@include slab; }

.title-h1 {
	margin-bottom: 50px;
	font-weight: 300;
	@include media-breakpoint-down(sm) {
		margin-bottom: 30px;
		font-size: 2rem; } }

.title-h2 {
	@include media-breakpoint-down(sm) {
		font-size: 1.7rem; } }


/* icons */
.icon {
	display: inline-block;
	vertical-align: middle; }

.icon-phone {
	@include s(phone); }
.icon-email {
	@include s(email); }

.back-up {
	position: fixed;
	z-index: 10;
	display: block;
	width: 40px;
	height: 40px;
	right: 20px;
	bottom: 20px;
	background: $white;
	border-radius: 50%;
	opacity: 0;
	visibility: hidden;
	box-shadow: 0 1px 5px 0 rgba($black, 0.7);
	&.is-visible {
		opacity: 1;
		visibility: visible; } }


.button {
	padding: 10px 15px;
	color: $white;
	background-color: $base-color;
	border-radius: 4px;
	letter-spacing: 0.1px;
	@include tr(background-color 0.2s ease);
	&:hover {
		background-color: lighten($base-color, 10%); } }


.figure {
	&--max-w {
		max-width: 500px; } }
