.header {
	background: $base-color;
	border-bottom: 1px solid $help-color;
	&__top {
		padding: 5px 0 13px;
		background-color: #6A4299;
		@include g(#6A4299 85%, #401870 100%); }
	&__bottom {
		padding: 10px 0; }
	&__link {
		color: $white;
		&:hover {
			color: inherit; }
		span {
			vertical-align: middle; } }
	&__contact {
		display: inline-block;
		margin-right: 20px;
		vertical-align: middle;
		color: $white; }
	.icon {
		margin-right: 10px; } }


.logo {
	max-width: 200px; }

.navigation {
	@include media-breakpoint-down(xs) {
		width: 100%; }
	@include media-breakpoint-down(sm) {
		position: fixed;
		z-index: 10;
		top: 0;
		right: 0;
		bottom: 0;
		width: 250px;
		overflow: auto;
		background: $white;
		color: $black;
		opacity: 0;
		transform: translate3d(110%, 0, 0);
		transition: opacity 0.3s ease, transform 0.5s ease;
		&.is-active {
			opacity: 1;
			transform: translate3d(0, 0, 0); } }
	&__list {
		display: flex;
		justify-content: space-between;
		margin-bottom: 0;
		@include media-breakpoint-down(sm) {
			display: block; } }
	&__option {
		position: relative;
		&:hover {
			.navigation-dropdown {
				opacity: 1;
				visibility: visible; } } }
	&__link {
		display: block;
		padding: 10px;
		text-transform: uppercase;
		color: $white;
		@include tr(color 0.4s linear);
		@include media-breakpoint-down(sm) {
			color: $black;
			border-bottom: 1px solid $light-grey;
			letter-spacing: 0.2px;
			&:hover {
				color: $black;
				background: $light-grey; } }
		&:hover {
			color: $help-color;
			.navigation__el {
				&:before {
					transform: translateX(0); } } }
		&--pointer {
			cursor: pointer;
			@include media-breakpoint-down(sm) {
				border-bottom: none; }
			.navigation__el {
				&:before {
					display: none; } } } }
	&__el {
		position: relative;
		display: block;
		padding-bottom: 5px;
		overflow: hidden;
		@include media-breakpoint-down(sm) {
			padding-bottom: 0; }
		&:before {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 1px;
			background: $white;
			transform: translateX(-105%);
			@include tr(transform 0.2s ease);
			@include media-breakpoint-down(sm) {
				display: none; } } } }


.navigation-dropdown {
	position: absolute;
	z-index: 10;
	background: $white;
	top: 100%;
	left: 10px;
	min-width: 180px;
	border-radius: 4px;
	border-top: 5px solid $accent-color;
	opacity: 0;
	visibility: hidden;
	box-shadow: 0 8px 15px rgba(0, 0, 0, 0.1);
	transition: opacity 0.2s ease, visibility 0.2s ease;
	@include media-breakpoint-down(sm) {
		position: static;
		opacity: 1;
		visibility: visible;
		box-shadow: none;
		border-top-color: $base-color;
		border-bottom: 5px solid $base-color;
		border-radius: 0; }
	&__option {
		&:last-child {
			.navigation-dropdown__link {
				border-radius: 0 0 4px 4px; } } }
	&__link {
		display: block;
		padding: 8px 15px;
		color: $black;
		font-size: 15px;
		font-weight: 700;
		line-height: 1.2;
		&:hover {
			background: lighten(#E5E9EF, 3%);
			color: inherit; }
		@include media-breakpoint-down(sm) {
			font-weight: normal; } } }


.nav-icon {
	position: relative;
	display: inline-block;
	width: 30px;
	height: 20px;
	&:before,
	&:after {
		content: "";
		position: absolute;
		left: 0;
		width: 100%;
		height: 2px;
		background: $white; }
	&:before {
		top: 0; }
	&:after {
		bottom: 0; }
	span {
		position: absolute;
		top: 50%;
		left: 0;
		margin-top: -1px;
		width: 100%;
		height: 2px;
		background: $white; } }


.close-icon {
	position: relative;
	display: inline-block;
	width: 30px;
	height: 20px;
	margin: 10px 10px 10px 0;
	&:hover {
		&:before,
		&:after {
			background: $accent-color; } }
	&:before,
	&:after {
		content: "";
		position: absolute;
		top: 10px;
		left: 0;
		width: 100%;
		height: 2px;
		background: $base-color;
		@include tr(background 0.2s ease); }
	&:before {
		@include rotate(45); }
	&:after {
		@include rotate(-45); } }
