.hero {
	&__img {
		height: 525px;
		background-size: cover;
		background-position: 50% 0%;
		background-repeat: no-repeat;
		@include media-breakpoint-up(lg) {
			height: 560px; }
		@include media-breakpoint-down(md) {
			height: 400px; }
		@include media-breakpoint-down(sm) {
			height: 350px; } } }


.content {
	background: $light-grey;
	padding: 50px 0 40px;
	color: #3B3444;
	@include media-breakpoint-up(sm) {
		padding: 70px 0 50px; }
	&__in--center {
		max-width: 1000px;
		margin: 0 auto; }
	&__img {
		max-width: 500px;
		&--top-left {
			display: block;
			margin: 0 auto 15px;
			@include media-breakpoint-up(md) {
				margin-right: 20px; } } }
	&__list {
		margin: 20px 0;
		list-style-type: disc;
		padding-left: 30px;
		li {
			margin-bottom: 5px;
			&:last-child {
				margin-bottom: 0; } } }
	&__link {
		margin: 0 1px;
		padding: 2px 3px 4px;
		background: $base-color;
		color: $white;
		&:hover,
		&:active,
		&:visited {
			color: $white; } } }


/* features */
.feature-card {
	position: relative;
	max-width: 300px;
	min-height: 250px;
	margin: 0 auto 30px;
	padding: 30px 0;
	background-color: $help-color;
	@include cover;
	border-radius: 4px;
	transition: transform .2s ease;
	&:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: rgba(#696969, 0.6); }
	&:hover {
		transform: translate3d(0, -2px, 0);
		.feature-card__text {
			&:before {
				border-color: $accent-color; } } }
	&__el {
		position: relative;
		width: 100%;
		padding: 0 10px;
		text-align: center;
		color: $white; }
	&__text {
		position: relative;
		margin-bottom: 0;
		font-size: 19px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 0.7px;
		&:before {
			content: "";
			position: absolute;
			top: 100%;
			left: 50%;
			width: 20%;
			margin-top: 15px;
			margin-left: -10%;
			border-bottom: 2px solid $white;
			transition: border-color .4s linear; } } }


/* services */
.services {
	@include media-breakpoint-down(sm) {
		text-align: center;
		margin-top: 20px; } }


.service-list {
	max-width: 350px;
	text-align: left;
	background: $white;
	margin-bottom: 0;
	padding: 20px 15px;
	border-radius: 4px;
	box-shadow: 0 8px 15px rgba(0, 0, 0, 0.1);
	@include media-breakpoint-down(sm) {
		margin: 0 auto; }
	&__option {
		position: relative;
		margin-bottom: 5px;
		padding-left: 15px;
		&:before {
			content: "";
			position: absolute;
			width: 8px;
			height: 3px;
			top: 10px;
			left: 0;
			background: $base-color; } }
	&__link {
		position: relative;
		color: $black;
		border-bottom: 1px solid transparent;
		&:hover {
			color: $black;
			border-color: $black; }
		&.is-active {
			color: $base-color;
			&:hover {
				border-bottom-color: transparent; } } } }


/* useful links */
.useful-link {
	text-align: center;
	margin-bottom: 40px;
	@include media-breakpoint-down(sm) {
		margin-bottom: 100px; }
	&:hover {
		.useful-link__text {
			opacity: 1;
			visibility: visible; } }
	&__logo {
		display: block; }
	&__text {
		margin-top: 20px;
		font-size: 17px;
		opacity: 0;
		visibility: hidden;
		@include tr(all 0.3s ease);
		@include media-breakpoint-down(sm) {
			visibility: visible;
			opacity: 1; } } }


.map {
	iframe {
		width: 100%; } }

/* styles for sticky-sidebar to improve performance and prevent repainting on scrolling */
.sidebar {
	will-change: min-height; }

.sidebar__inner {
	transform: translate(0, 0) /* For browsers that don't support translate3d. */;
	transform: translate3d(0, 0, 0);
	will-change: position, transform; }
