.close-icon:hover:before, .close-icon:hover:after { background: #ffa800; }

.close-icon:before, .close-icon:after { content: ""; position: absolute; top: 10px; left: 0; width: 100%; height: 2px; background: #55298A; transition: background 0.2s ease; }

.close-icon:before { transform: rotate(45deg); }

.close-icon:after { transform: rotate(-45deg); }

.footer__top { padding: 20px 20px 50px; background-color: #e2dcec; background-image: linear-gradient(-220deg, #55298A, #e2dcec); }

.footer__bottom { padding: 30px 0 10px; font-size: 14px; color: #fff; background: #3b3444; }

.footer__info { margin-bottom: 30px; }

.footer__nav { padding-left: 40px; }

@media (max-width: 767.98px) { .footer__nav { padding: 30px 0 0; border-top: 1px solid #fff; } }

.footer__highlight { color: #ffa800; font-style: italic; border-bottom: 1px solid #ffa800; }

.footer__link { color: #fff; border-bottom: 1px solid transparent; }

.footer__link:hover { color: inherit; border-bottom-color: #fff; }

.footer__option { margin: 0 10px 10px 0; }

@media (min-width: 576px) { .footer__option { display: inline-block; } }

.hero__img { height: 525px; background-size: cover; background-position: 50% 0%; background-repeat: no-repeat; }

@media (min-width: 992px) { .hero__img { height: 560px; } }

@media (max-width: 991.98px) { .hero__img { height: 400px; } }

@media (max-width: 767.98px) { .hero__img { height: 350px; } }

.content { background: #eee; padding: 50px 0 40px; color: #3B3444; }

@media (min-width: 576px) { .content { padding: 70px 0 50px; } }

.content__in--center { max-width: 1000px; margin: 0 auto; }

.content__img { max-width: 500px; }

.content__img--top-left { display: block; margin: 0 auto 15px; }

@media (min-width: 768px) { .content__img--top-left { margin-right: 20px; } }

.content__list { margin: 20px 0; list-style-type: disc; padding-left: 30px; }

.content__list li { margin-bottom: 5px; }

.content__list li:last-child { margin-bottom: 0; }

.content__link { margin: 0 1px; padding: 2px 3px 4px; background: #55298A; color: #fff; }

.content__link:hover, .content__link:active, .content__link:visited { color: #fff; }

/* features */
.feature-card { position: relative; max-width: 300px; min-height: 250px; margin: 0 auto 30px; padding: 30px 0; background-color: #e2dcec; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; border-radius: 4px; transition: transform .2s ease; }

.feature-card:before { content: ""; position: absolute; top: 0; left: 0; right: 0; bottom: 0; background: rgba(105, 105, 105, 0.6); }

.feature-card:hover { transform: translate3d(0, -2px, 0); }

.feature-card:hover .feature-card__text:before { border-color: #ffa800; }

.feature-card__el { position: relative; width: 100%; padding: 0 10px; text-align: center; color: #fff; }

.feature-card__text { position: relative; margin-bottom: 0; font-size: 19px; font-weight: 700; text-transform: uppercase; letter-spacing: 0.7px; }

.feature-card__text:before { content: ""; position: absolute; top: 100%; left: 50%; width: 20%; margin-top: 15px; margin-left: -10%; border-bottom: 2px solid #fff; transition: border-color .4s linear; }

/* services */
@media (max-width: 767.98px) { .services { text-align: center; margin-top: 20px; } }

.service-list { max-width: 350px; text-align: left; background: #fff; margin-bottom: 0; padding: 20px 15px; border-radius: 4px; box-shadow: 0 8px 15px rgba(0, 0, 0, 0.1); }

@media (max-width: 767.98px) { .service-list { margin: 0 auto; } }

.service-list__option { position: relative; margin-bottom: 5px; padding-left: 15px; }

.service-list__option:before { content: ""; position: absolute; width: 8px; height: 3px; top: 10px; left: 0; background: #55298A; }

.service-list__link { position: relative; color: #000; border-bottom: 1px solid transparent; }

.service-list__link:hover { color: #000; border-color: #000; }

.service-list__link.is-active { color: #55298A; }

.service-list__link.is-active:hover { border-bottom-color: transparent; }

/* useful links */
.useful-link { text-align: center; margin-bottom: 40px; }

@media (max-width: 767.98px) { .useful-link { margin-bottom: 100px; } }

.useful-link:hover .useful-link__text { opacity: 1; visibility: visible; }

.useful-link__logo { display: block; }

.useful-link__text { margin-top: 20px; font-size: 17px; opacity: 0; visibility: hidden; transition: all 0.3s ease; }

@media (max-width: 767.98px) { .useful-link__text { visibility: visible; opacity: 1; } }

.map iframe { width: 100%; }

/* styles for sticky-sidebar to improve performance and prevent repainting on scrolling */
.sidebar { will-change: min-height; }

.sidebar__inner { transform: translate(0, 0); transform: translate3d(0, 0, 0); will-change: position, transform; }
